import React from 'react';
import logo from './logo.svg';
import './App.css';
import BusinessList from './components/BusinessList/BusinessList';
import SearchBar from './components/SearchBar/SearchBar';
import Yelp from './util/Yelp'


 const business = {
  imageSrc: 'https://s3.amazonaws.com/codecademy-content/programs/react/ravenous/pizza.jpg',
  name: 'MarginOtto Pizzeria',
  address: '1010 Paddington Way',
  city: 'Flavortown',
  state: 'NY',
  zipCode: '10101',
  category: 'Italian',
  rating: 4.5,
  reviewCount: 90
}; 
let businesses1 = [business, business, business, business, business, business] 
 
class App extends React.Component {
  constructor(props){
    super(props);

    this.state= {
      businesses: [],
    };
    this.searchYelp = this.searchYelp.bind(this);
  }

  searchYelp(term, location, sortBy){
    if(term=== "" || location=== ""){
      //
      window.alert("A searchbox is empty. Please fill in location and business");
      return;
    }
    console.log(`Searching Yelp with ${term}, ${location}, ${sortBy}`);
    //this.setState({businesses: businesses1});
    Yelp.search(term, location, sortBy).then(businesses => {
      //console.log(businesses.count);
      this.setState({businesses: businesses});
    });
  }

  render(){
    return (
      <div className="App">
        <h1>ravenous</h1>
        <SearchBar searchYelp={this.searchYelp} />
        < BusinessList businesses={this.state.businesses} />
    </div>
    );
  }
}

export default App;